@charset "utf-8";

* {
  margin: 0;
  padding: 0; }

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent; }

body {
    line-height: 1; }

article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {
    display: block; }

nav ul {
    list-style: none; }

blockquote, q {
    quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none; }
a {
    margin: 0;
    padding: 0;
    font-size: 100%;
    vertical-align: baseline;
    background: transparent; }

ins {
    background-color: #ff9;
    color: #000;
    text-decoration: none; }

mark {
    background-color: #ff9;
    color: #000;
    font-style: italic;
    font-weight: bold; }

del {
    text-decoration: line-through; }

abbr[title], dfn[title] {
    border-bottom: 1px dotted;
    cursor: help; }

table {
    border-collapse: collapse;
    border-spacing: 0; }

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #cccccc;
    margin: 1em 0;
    padding: 0; }

input, select {
    vertical-align: middle; }

a {
  color: inherit;
  transition: opacity 0.2s;
  text-decoration: none;
  &:hover {
    opacity: 0.9;
    text-decoration: none; } }

li {
  list-style: none; }

sup {
  vertical-align: 0.5em;
  font-size: 0.8em; }

.clearfix {
  zoom: 1;
  &:after {
    clear: both;
    display: block;
    content: ""; } }

.slick-slider {
    position: relative;

    display: block;
    box-sizing: border-box;

    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent; }

.slick-list {
    position: relative;

    display: block;
    // overflow: hidden
    margin: 0;
    padding: 0; }

.slick-list:focus {

    outline: none; }

.slick-list.dragging {
    cursor: pointer;
    cursor: hand; }


.slick-slider .slick-track,
.slick-slider .slick-list {
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0); }

.slick-track {

    position: relative;
    top: 0;
    left: 0;

    display: block; }

.slick-track:before,
.slick-track:after {

    display: table;

    content: ''; }

.slick-track:after {

    clear: both; }

.slick-loading .slick-track {

    visibility: hidden; }


.slick-slide {

    display: none;
    float: left;

    /*height: 100%*/
    /*min-height: 1px*/ }

[dir='rtl'] .slick-slide {

    float: right; }

.slick-slide img {

    display: block; }

.slick-slide.slick-loading img {

    display: none; }

.slick-slide.dragging img {

    pointer-events: none; }

.slick-initialized .slick-slide {

    display: block; }

.slick-loading .slick-slide {

    visibility: hidden; }

.slick-vertical .slick-slide {

    display: block;

    height: auto;

    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
    display: none; }

/* Arrows */
.slick-prev,
.slick-next {

    font-size: 0;
    line-height: 0;

    position: absolute;
    top: 50%;
    margin-top: -30px;
    display: none;

    padding: 0;
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: #fff;

    width: 25px;
    height: 60px;
    text-indent: -99999px; }

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {

    background-position: left bottom; }

.slick-prev.slick-disabled,
.slick-next.slick-disabled {

    opacity: 0; }


.slick-next {

    background: #f0f;
    background: url('/assets/img/btn_next.png') left top no-repeat;
    background-size: 25px 60px;
    margin-left: 0px;
    z-index: 1000;
    right: 0px; }


.slick-prev {

    background: #ff0;
    background: url(/assets/img/btn_prev.png) left top no-repeat;
    background-size: 25px 60px;
    margin-left: 0px;
    left: 0px;
    z-index: 1000; }


/* Dots */
.slick-dotted.slick-slider {

    /*margin-bottom: 128px*/ }


.slick-dots {

    display: block;
    width: 100%;
    height: 15px;
    line-height: 0px;
    margin-top: 0px;
    list-style: none;
    text-align: left;
    position: absolute;
    bottom: 23px;
    left: 23px;
    // bottom: -24px
 }    // top: -26px

.slick-dots li {

    position: relative;
    display: inline-block;
    width: 15px;
    height: 15px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;

    &.slick-active {
      button {
        &:before {
          width: 15px;
          height: 15px;
          border-radius: 100%;
          background: #000000; } } }

    button {

      font-size: 0;
      line-height: 0;

      display: block;
      width: 15px;
      height: 15px;
      cursor: pointer;
      color: transparent;
      border: none;
      outline: none;
      background: none;
      opacity: 1.0;
      position: relative;
      &:before {
        content: "";
        display: block;
        width: 15px;
        height: 15px;
        border-radius: 100%;
        background: #FFFFFF;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%); } } }


@import "_vars";
@import "_pc";
@import "_sp";
