
@media only screen and (max-width: $breakpoint1  - 1) {

  .is-pc {
    display: none !important; }

  html {
    scroll-padding-top: calc(52px + 40px);
    scroll-behavior: smooth;
    overflow-x: hidden;
    width: 100%; }

  body {
    font-family: "Instrument Sans", "Noto Sans JP", sans-serif;
    line-height: 1.75;
    overflow-x: hidden;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: $defaultLS;
    -webkit-font-smoothing: antialiased;
    font-feature-settings: "palt";
    width: 100%;
    background: #E8E8E8;
 }    // height: 100%

  header {
    position: fixed;
    width: 100%;
    height: 52px;
    top: 0px;
    left: 0px;
    z-index: 100;
    background: white;
    border-bottom: 1px solid $blue;
    transform: translate(0px,-100%);
    transition: transform 0.2s $easeOutQuad;
    &.scroll {
      transform: translate(0px,0%); }
    .inner {
      .main {
        display: none; }
      .logo {
        position: absolute;
        left: 50%;
        top: 8px;
        transform: translate(-50%,0px);
        img {
          width: 120px; } }
      .lang {
        display: none; } } }

  footer {
    width: 100%;
    padding-top: 40px;
    padding-bottom: 40px;
    // padding-left: 40px
    // padding-right: 40px
    box-sizing: border-box;
    background: #E8E8E8;
    text-align: center;
    .inner {
      width: 100%;
      .copy {
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: normal; }
      a {
        display: inline-block;
        border-bottom: 1px solid;
        margin-bottom: 17px;
        font-size: 13px;
        font-style: normal;
        font-weight: 400;
        line-height: normal; } } }

  .lang {
    position: fixed;
    top: 13px;
    right: 18px;
    border: 1px solid $blue;
    // display: flex
    justify-content: center;
    align-items: center;
    z-index: 10001;
    pointer-events: auto;
    &.scroll {
      .lang-item {
        background: white;
        color: $blue;
        &.current {
          background: $blue;
          color: white;
          &:after {
            border-color: white transparent transparent transparent; } } }
      a {
        border: none; } }
    &.show {
      .lang-item {
        display: flex;
        pointer-events: auto; } }
    .lang-item {
      display: block;
      width: 45px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: $blue;
      color: white;
      pointer-events: none;
      display: none;
      box-sizing: border-box;
      font-size: 13px;
      position: relative;
      &.current {
        background: white;
        color: $blue;
        display: flex;
        &:after {
          display: block;
          content: "";
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 4.3px 2.5px 0 2.5px;
          border-color: $blue transparent transparent transparent;
          position: absolute;
          bottom: 5px;
          right: 5px; } } }
    a {
      border: 1px solid; } }

  .btn-menu {
    display: block;
    position: fixed;
    top: 0px;
    left: 22px;
    width: 27px;
    height: 52px;
    box-sizing: border-box;
    z-index: 10000;
    // transform: translate(-50% , 0px)
    display: flex;
    justify-content: center;
    align-items: center;
    transition: height 0.2s $easeOutQuad;
    &.scroll {
      span {
        background: $blue; } }
    &.active {
      span {
        background: white !important;
        &:first-child {
          transform: translate(-50%,calc(-50%)) rotate(45deg); }
        &:nth-child(2) {
          transform: scale(0); }
        &:last-child {
          transform: translate(-50%,calc(-50%)) rotate(-45deg); } } }
    &.short {
      padding-top: 10px;
      padding-bottom: 10px;
      height: 38px;
      box-sizing: content-box;
 }      // height: 38px
    .icon {
      width: 27px;
      height: 27px;
      display: block;
      position: relative; }
    span {
      display: block;
      width: 27px;
      height: 1px;
      background: white;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      transition: transform 0.2s $easeOutQuad;
      &:first-child {
        transform: translate(-50%,calc(-50% - 5px)); }
      &:last-child {
        transform: translate(-50%,calc(-50% + 5px)); } } }

  .menu {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: calc(100%);
    overflow-x: hidden;
    overflow-y: auto;
    box-sizing: border-box;
    z-index: 100;
    background: $blue;
    color: white;
    pointer-events: none;
    opacity: 0.0;
    transition: opacity 0.2s $easeOutQuad;
    z-index: 1000;
    &.show {
      opacity: 1.0;
      pointer-events: auto; }
    .inner {
      display: table;
      height: 100%;
      margin: 0 auto;
      position: relative;
      .menu-content {
        display: table-cell;
        vertical-align: top;
        > div {
          margin: 0 auto;
          position: relative;
          .logo {
            margin-top: 74px;
            img {
              width: 205.47px; } }
          .main {
            text-align: center;
            margin-top: 74px;
            ul {
              li {
                margin-bottom: 24px;
                &:last-child {
                  margin-bottom: 0px; }
                a {
                  display: inline-block; } } } } } } } }

  .container {
    background: white;
    &.index {
      &:before {
        content: "";
        display: block;
        width: 100vw;
        height: 710px;
        background: #DDE8FF;
        position: absolute;
        top: 0px;
        left: 0px;
        clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 120px), 0% 100%); }
      &:after {
        content: "";
        display: block;
        width: 100vw;
        height: 430px;
        background: #00236D;
        position: absolute;
        top: 0px;
        left: 0px;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% calc(100% - 120px)); } } }
  .content {
    padding-top: 180px;
    padding-left: 22px;
    padding-right: 22px;
    max-width: 600px;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    section.kv {
      // padding-top: 235px
      padding-bottom: 240px;
      text-align: center;
      img {
        width: 231px; } }

    section {
      padding-bottom: 100px;
      .inner {
        h2 {
          text-align: center;
          margin-bottom: 60px;
          position: relative;
          &:after {
            content: "";
            display: block;
            width: 6px;
            height: 1px;
            background: $blue;
            position: absolute;
            bottom: -30px;
            left: 50%;
            transform: translate(-50%,0px); } }
        > .desc {
          font-size: 15px;
          font-style: normal;
          font-weight: 500;
          line-height: 160%; /* 24px */;
          letter-spacing: 0.75px;
          margin-bottom: 30px;
          &:last-child {
            margin-bottom: 0px; } }
        > .desc2 {
          color: $blue;
          font-size: 13px;
          font-style: normal;
          font-weight: 600;
          line-height: 150%; /* 19.5px */;
          margin-bottom: 30px;
          &:last-child {
            margin-bottom: 0px; } }
        > .txt {
          font-size: 13px;
          font-style: normal;
          font-weight: 400;
          line-height: 180%; /* 28.8px */;
          letter-spacing: 0.8px; }
        > .items {
          .item {
            margin-bottom: 40px;
            .head {
              display: flex;
              justify-content: flex-start;
              gap: 10px;
              font-size: 15px;
              font-style: normal;
              font-weight: 600;
              line-height: 180%; /* 27px */;
              letter-spacing: 0.75px;
              margin-bottom: 12px;
              color: $blue; }
            .desc {
              font-size: 13px;
              font-style: normal;
              font-weight: 400;
              line-height: 160%; /* 20.8px */;
              letter-spacing: 0.65px; } } }
        ul {
          border-left: 1px solid $blue;
          padding-left: 1.5em;
          margin-bottom: 40px;
          &:last-child {
            margin-bottom: 0px; }
          li {
            list-style-type: disc;
            margin-left: 1em;
            font-size: 13px;
            font-style: normal;
            font-weight: 400;
            line-height: 160%; /* 20.8px */;
            letter-spacing: 0.65px;
            margin-bottom: 20px; } }
        .table {
          margin-bottom: 80px;
          &:last-child {
            margin-bottom: 0px; }
          .item {
            display: flex;
            font-size: 13px;
            font-style: normal;
            font-weight: 400;
            line-height: 150%; /* 19.5px */;
            .head {
              width: 100px;
              border-right: 1px solid $blue;
              padding-top: 2px;
              padding-bottom: 2px;
              color:  $blue;
              font-weight: 600; }
            .body {
              width: calc(100% - 100px);
              padding-left: 40px;
              box-sizing: border-box;
              padding-top: 2px;
              padding-bottom: 2px;
              line-height: 200%;
              a {
                color:  $blue;
                display: inline-flex;
                justify-content: flex-start;
                align-items: center;
                gap: 4px;
                border-bottom: 1px solid; } } } } } }

    section.lead {
      margin-bottom: 180px;
      .inner {
        h2 {
          text-align: center;
          margin-bottom: 22px;
          padding-left: 10px;
          padding-right: 10px;
          &:after {
            display: none; }
          img {
            width: 328px; } }
        .desc {
          text-align: center;
          color: $blue;
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 150%; /* 21px */; } } }

    section.mission {
      .inner {
        h2 {
          img {
            width: 157px; } } } }

    section.goal {
      .inner {
        h2 {
          img {
            width: 104px; } } } }

    section.approach {
      .inner {
        h2 {
          img {
            width: 214px; } } } }

    section.company {
      .inner {
        h2 {
          img {
            width: 180px; } } } }


    section.inquiries {
      .inner {
        h2 {
          img {
            width: 184px; } } } }

    section.terms {
      .inner {
        h2 {
          img {
            width: 287px; } } } }

    section.ceo {
      .inner {
        h2 {
          font-size: 13px;
          font-style: normal;
          font-weight: 500;
          line-height: 180%; /* 36px */;
          letter-spacing: 1px;
          text-align: left;
          color: $blue;
          margin-bottom: 0px;
          &:after {
            display: none; } }

        .name {
          font-size: 24px;
          font-style: normal;
          font-weight: 600;
          line-height: 180%; /* 52.2px */;
          letter-spacing: 1.45px;
          margin-bottom: 30px;
          color: $blue; }
        .profile-main {
          font-size: 13px;
          font-style: normal;
          font-weight: 400;
          line-height: 180%; /* 28.8px */;
          letter-spacing: 0.8px;
          margin-bottom: 18px; }
        .profile-sub {
          font-size: 12px;
          font-style: normal;
          font-weight: 400;
          line-height: 180%; /* 25.2px */;
          letter-spacing: 0.7px; } } } } }
