

@media only screen and (min-width: $breakpoint1) {

  .is-sp {
    display: none !important; }

  html {
    scroll-padding-top: calc(90px + 40px);
    scroll-behavior: smooth;
    width: 100vw;
    overflow-x: hidden; }

  body {
    font-family: "Instrument Sans", "Noto Sans JP", sans-serif;
    line-height: 1.75;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: $defaultLS;
    -webkit-font-smoothing: antialiased;
    font-feature-settings: "palt";
    width: 100vw;
    overflow-x: hidden;
    background: #E8E8E8;
 }    // color: #404040
  header {
    position: fixed;
    width: 100%;
    height: 90px;
    top: 0px;
    left: 0px;
    padding-left: 40px;
    padding-right: 40px;
    box-sizing: border-box;
    // background: white
    // border-bottom: 1px solid $blue
    z-index: 100;
    &.scroll {
      &:before {
        transform: translate(0px,0px); }
      .inner {
        .logo {
          transform: translate(0px,0px); }
        .lang {
          a {
            border: none;
            &.current {
              border: 1px solid; } } }
        .main {
          ul {
            a {
              color: $blue; } } } } }
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background: white;
      border-bottom: 1px solid;
      position: absolute;
      left: 0px;
      top: 0px;
      transform: translate(0px,-90px);
 }      // transition: transform 0.2s $easeOutQuad
    .inner {
      width: 100%;
      height: 100%;
      max-width: 1400px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      gap: 40px;
      .logo {
        // position: absolute
        display: block;
        top: 50%;
        left: 0px;
        transform: translate(0px,-90px);
 }        // transition: transform 0.2s $easeOutQuad
      .lang {
        // position: absolute
        top: 50%;
        right: 0px;
        // transform: translate(0px,-50%)
        // border: 1px solid $blue
        display: flex;
        justify-content: center;
        align-items: center;
        a {
          display: block;
          width: 50px;
          height: 24px;
          display: flex;
          justify-content: center;
          align-items: center;
          background: $blue;
          color: white;
          border: 1px solid;
          box-sizing: border-box;
          &.current {
            border: none;
            background: white;
            color: $blue; } } }
      .main {
        ul {
          display: flex;
          gap: 30px;
          justify-content: center;
          align-items: center;
          a {
            display: block;
            font-size: 14px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
            color: white; } } } } }

  footer {
    width: 100%;
    height: 160px;
    padding-left: 40px;
    padding-right: 40px;
    box-sizing: border-box;
    background: #E8E8E8;
    .inner {
      width: 100%;
      height: 100%;
      max-width: 1400px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row-reverse;
      .copy {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal; }
      a {
        display: block;
        border-bottom: 1px solid;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal; } } }
  .container {
    background: white;
    &.index {
      header {
        &:before {
          transition: transform 0.2s $easeOutQuad; }
        .inner {
          .logo {
            transition: transform 0.2s $easeOutQuad; } } }
      &:before {
        content: "";
        display: block;
        width: 100vw;
        height: 1010px;
        background: #DDE8FF;
        position: absolute;
        top: 0px;
        left: 0px;
        clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 250px), 0% 100%); }
      &:after {
        content: "";
        display: block;
        width: 100vw;
        height: 680px;
        background: #00236D;
        position: absolute;
        top: 0px;
        left: 0px;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% calc(100% - 250px)); } } }


  .content {
    padding-top: 235px;
    width: 870px;
    margin: 0 auto;
    // background: white
    position: relative;
    z-index: 1;

    section.kv {
      // padding-top: 235px
      padding-bottom: 300px;
      text-align: center; }
    section {
      padding-bottom: 120px;
      .inner {
        h2 {
          text-align: center;
          margin-bottom: 60px;
          position: relative;
          &:after {
            content: "";
            display: block;
            width: 6px;
            height: 1px;
            background: $blue;
            position: absolute;
            bottom: -30px;
            left: 50%;
            transform: translate(-50%,0px); } }
        > .desc {
          font-size: 22px;
          font-style: normal;
          font-weight: 500;
          line-height: 180%; /* 39.6px */;
          letter-spacing: 1.1px;
          margin-bottom: 40px;
          &:last-child {
            margin-bottom: 0px; } }
        > .desc2 {
          color: $blue;
          font-size: 18px;
          font-style: normal;
          font-weight: 600;
          line-height: 160%; /* 28.8px */;
          letter-spacing: 0.9px;
          margin-bottom: 40px;
          &:last-child {
            margin-bottom: 0px; } }
        > .txt {
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 180%; /* 28.8px */;
          letter-spacing: 0.8px;
          margin-bottom: 120px;
          ul {
            margin-bottom: 1em;
            margin-top: 1em;
            &.id {
              padding-left: 1em; }
            &:first-child {
              margin-top: 0px; }
            &:last-child {
              margin-top: 1em; }
            &:last-child {
              margin-bottom: 0px; }
            li {
              list-style-type: disc;
              margin-left: 1em;
 } }              // margin-bottom: 1em
          &:last-child {
            margin-bottom: 0px; } }
        > .items {
          .item {
            margin-bottom: 60px;
            .head {
              display: flex;
              justify-content: flex-start;
              gap: 10px;
              font-size: 22px;
              font-style: normal;
              font-weight: 600;
              line-height: 180%; /* 39.6px */;
              letter-spacing: 1.1px;
              margin-bottom: 20px;
              color: $blue; }
            .desc {
              font-size: 18px;
              font-style: normal;
              font-weight: 400;
              line-height: 160%; /* 28.8px */; } } }
        > ul {
          border-left: 1px solid $blue;
          padding-left: 1.5em;
          margin-bottom: 80px;
          &:last-child {
            margin-bottom: 0px; }
          li {
            list-style-type: disc;
            margin-left: 1em;
            font-size: 18px;
            font-style: normal;
            font-weight: 400;
            line-height: 160%; /* 28.8px */;
            letter-spacing: 0.9px;
            margin-bottom: 20px; } }
        .table {
          margin-bottom: 80px;
          &:last-child {
            margin-bottom: 0px; }
          .item {
            display: flex;
            font-size: 18px;
            font-style: normal;
            line-height: 200%; /* 36px */;
            font-weight: 400;
            .head {
              width: 100px;
              border-right: 1px solid $blue;
              padding-top: 5px;
              padding-bottom: 5px;
              color:  $blue;
              font-weight: 600; }
            .body {
              width: calc(100% - 100px);
              padding-left: 40px;
              box-sizing: border-box;
              padding-top: 5px;
              padding-bottom: 5px;
              line-height: 200%;
              a {
                color:  $blue;
                display: inline-flex;
                justify-content: flex-start;
                align-items: center;
                gap: 4px;
                border-bottom: 1px solid; } } } } } }

    section.lead {
      margin-bottom: 240px;
      .inner {
        h2 {
          text-align: center;
          margin-bottom: 22px;
          &:after {
            display: none; } }
        .desc {
          text-align: center;
          color: $blue;
          font-size: 18px;
          font-style: normal;
          font-weight: 500;
          line-height: 199.5%; } } }


    section.mission {
      margin-bottom: 120px; }

    section.ceo {
      .inner {
        h2 {
          font-size: 20px;
          font-style: normal;
          font-weight: 500;
          line-height: 180%; /* 36px */;
          letter-spacing: 1px;
          text-align: left;
          margin-bottom: 1px;
          color: $blue;
          &:after {
            display: none; } }
        .name {
          font-size: 29px;
          font-style: normal;
          font-weight: 600;
          line-height: 180%; /* 52.2px */;
          letter-spacing: 1.45px;
          margin-bottom: 50px;
          color: $blue; }
        .profile-main {
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 180%; /* 28.8px */;
          letter-spacing: 0.8px;
          margin-bottom: 20px; }
        .profile-sub {
          font-size: 14px;
          font-style: normal;
          font-weight: 400;
          line-height: 180%; /* 25.2px */;
          letter-spacing: 0.7px; } } } } }

@media only screen and (min-width: $breakpoint2) {
  header {
    &.scroll {
      .inner {
        .logo {
          transform: translate(0px,calc(-50%)); } } }
    .inner {
      justify-content: center;
      .logo {
        position: absolute;
        transform: translate(0px,calc(-90px)); }
      .lang {
        position: absolute;
        transform: translate(0px,-50%); }
      .main {
        ul {
          gap: 40px;
          a {
            font-size: 18px; } } } } }
  .container {
    &.index {
      &:before {
        height: 1090px;
        clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 390px), 0% 100%); }
      &:after {
        height: 750px;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% calc(100% - 390px)); } } } }
