@charset "utf-8";

//breakpoint
$breakpoint1: 1024px;
$breakpoint2: 1280px;
$breakpoint3: 1920px;
$defaultLS: 0.02em;
$blue: #00236D;
$sky: #9BE7FF;
$gray: #606066;
$lgray: #afafb2;
$red: #FF4040;

$fontEnHead: 'Poppins', sans-serif;

.txt-bl {
  color: #1D2088; }

.txt-rd {
  color: #E60012; }

//easing

$easeInSine:      cubic-bezier(0.47, 0, 0.745, 0.715);
$easeOutSine:     cubic-bezier(0.39, 0.575, 0.565, 1);
$easeInOutSine:   cubic-bezier(0.445, 0.05, 0.55, 0.95);
$easeInQuad:      cubic-bezier(0.55, 0.085, 0.68, 0.53);
$easeOutQuad:     cubic-bezier(0.25, 0.46, 0.45, 0.94);
$easeInOutQuad:   cubic-bezier(0.455, 0.03, 0.515, 0.955);
$easeInCubic:     cubic-bezier(0.55, 0.055, 0.675, 0.19);
$easeOutCubic:    cubic-bezier(0.215, 0.61, 0.355, 1);
$easeInOutCubic:  cubic-bezier(0.645, 0.045, 0.355, 1);
$easeInQuart:     cubic-bezier(0.895, 0.03, 0.685, 0.22);
$easeOutQuart:    cubic-bezier(0.165, 0.84, 0.44, 1);
$easeInOutQuart:  cubic-bezier(0.77, 0, 0.175, 1);
$easeInQuint:     cubic-bezier(0.755, 0.05, 0.855, 0.06);
$easeOutQuint:    cubic-bezier(0.23, 1, 0.32, 1);
$easeInOutQuint:  cubic-bezier(0.86, 0, 0.07, 1);
$easeInExpo:      cubic-bezier(0.95, 0.05, 0.795, 0.035);
$easeOutExpo:     cubic-bezier(0.19, 1, 0.22, 1);
$easeInOutExpo:   cubic-bezier(1, 0, 0, 1);
$easeInCirc:      cubic-bezier(0.6, 0.04, 0.98, 0.335);
$easeOutCirc:     cubic-bezier(0.075, 0.82, 0.165, 1);
$easeInOutCirc:   cubic-bezier(0.785, 0.135, 0.15, 0.86);
$easeInBack:      cubic-bezier(0.6, -0.28, 0.735, 0.045);
$easeOutBack:     cubic-bezier(0.175, 0.885, 0.32, 1.275);
$easeInOutBack:   cubic-bezier(0.68, -0.55, 0.265, 1.55);
$easeInOutGenerate: cubic-bezier(.42,.01,.2,1);

a {
  transition: opacity 0.2s $easeOutQuad;
  &:hover {
    opacity: 0.6; } }


@mixin pc-font-20 {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.8;
  letter-spacing: -0.4px; }

@mixin pc-font-16 {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.7;
  letter-spacing: -0.16px; }

@mixin pc-font-15 {
  font-size: 15px;
  font-weight: 500;
  line-height: normal;
  letter-spacing: -0.3px; }

@mixin pc-font-14 {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 162%; /* 22.68px */;
  letter-spacing: -0.14px; }

@mixin pc-font-14-mid {
  font-size: 14px;
  font-weight: 600;
  line-height: normal;
  letter-spacing: -0.28px; }

@mixin sp-font-12 {
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 164%; /* 19.68px */;
  letter-spacing: -0.12px; }

@mixin sp-font-13 {
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 162%; /* 21.06px */;
  letter-spacing: -0.13px; }

@mixin sp-font-15 {
  font-size: 15px;
  font-style: normal;
  // font-weight: 500;
  line-height: 170%; /* 25.5px */;
  letter-spacing: -0.15px; }

.split-text {
  opacity: 0.0;
  white-space: nowrap;
  em {
    font-style: normal;
    opacity: 0.0;
    transition: 0.4s $easeOutSine; } }

@keyframes expansion {
  0% {
    top: -40px; }
  100% {
    top: 40px; } }

@keyframes scales {
  0% {
    opacity: 1.0;
    transform: translate(-50%,0) scale(3,1); }
  100% {
    opacity: 0.0;
    transform: translate(-50%,0) scale(4.5,1.5); } }
